* {
  -webkit-box-sizing: border-box;
  /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;
  /* Firefox, other Gecko */
  box-sizing: border-box;
  /* Opera/IE 8+ */
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Courier New', Courier, monospace;
  /* font-family: 'Montserrat', 'Open Sans', 'Helvetica Neue', sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
  line-height: 1.6em;
}

code{
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul {
  list-style-type: none;
}

/* random links inside paragraphs must have this class */
.asper {
  text-decoration: underline dashed;
  text-decoration-color: transparent;
  transition: .3s;
  color: #EAA01F;
}

.asper:hover {
  color: #EAA01F;
  text-decoration: underline dashed;
}

/* coloring some spans */
.gold{
  color: #EAA01F;
}

.grey{
  color:#cfcfcf;
}

.dabba{
  background-color: #121212;
  padding: 2em;
  border-radius: 5px;
}

/* adding spacing under each object */

.big-bottom {
  margin-bottom: 32px;
}

.small-bottom {
  margin-bottom: 24px;
}

/* general */

image {
  user-select: none;
}

/* nav bar */

.nav {
  background-color: transparent;
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  height: 5em;
  width: 100%;
  z-index: .1;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: right;
  padding: 1.2em 0.8em 0 0;
}

.nav--logo{
  height: 4em;
  margin-left: 20px;
}

.nav--links {
  display: flex;
  list-style: none;
  width: 100%;
  justify-content: flex-end;
}

.nav--link {
  color: #fafafa;
  font-size: 1.2em;
  padding: 0.2em 1em;
  margin: 2px;
  border: 2px solid transparent;
  display: inline-block;
  text-decoration: none;
  font-weight: 500;
  transition: .3s;
}

.nav--link:hover {
  cursor: pointer;
  color: #EAA01F;
  border-bottom: 2px solid #EAA01F;
}

.nav--link:focus {
  color: #EAA01F;
}

.container {
  scroll-snap-type: y proximity;
  /* mandatory */
  overflow-y: scroll;
  height: 100vh;
}

.bod {
  min-height: 100vh;
  width: 100vw;
  padding: 120px 25px 25px;
  padding-left: 50px;
  display: flex;
  flex-direction: column;
  align-items: justify;
  justify-content: baseline;
  scroll-snap-align: start;
  font-size: 1.4em;

  background-color: #212121;
  color: #fafafa;
}

/* 
.bod1 {
  background-color: #fafafa;
  color: #212121;
} */
/* 
.bod2 {
  background-color: #212121;
  color: #fafafa;
} */

.contact--footer,
#contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

/* about */
#about--container {
  max-width: 45em;
}

/* icons */

.contact--icons>ul {
  display: flex;
  align-items: baseline;
  justify-content: center;
}

/* coloring and animating icons on hover */

.icons {
  height: 36px;
  filter: invert(36%);
  margin: 10px;
  transition: 0.3s;
}

.icons:hover {
  /* 
  margin-top: 2px; */
  padding: 2px;
  filter: invert(100%);
}

.to-top {
  height: 64px;
  position: fixed;
  bottom: 2em;
  right: 2em;
  transition: .3s;
}

.to-top:hover {
  cursor: pointer;
  filter: brightness(50%) invert(100%);
  padding: 4px;
}

/* cards */

.card--container{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 1em;
  justify-content: space-between;
}

.card {
  max-width: 400px;
  margin: 1em;
  border: 1px dashed transparent;
  border-radius: 5px;
  background-color: #121212;
  transition: .3s;
}

.card:hover{
  border:1px dashed #EAA01F;
}

.card--items {
  color: #fafafa;
  padding: 24px;
  display: inline-block;
}

.card--title {
  font-size: 24px;
  font-weight: bold;
  margin: 1vh 0px;
  padding: 0px 1px;
  word-wrap: break-word;
}

.card--about {
  display: block;
  font-size: 16px;
  font-family: 'Open Sans', 'Helvetica Neue', sans-serif;
  color: #cfcfcf;
}

.card--bottom {
  color: #cfcfcf;
  font-size: 16px;
  margin-top: 24px;
}

/* design */
.design--merch,
.design--socials{
  display: flex;
  flex-direction: column;
  align-items: center;
}
/* contact logo */
.contact--logo {
  height: 300px;
  margin: 50px;
}

/* email */
.contact--email {
  margin: 20px;
}

.email {
  color: #EAA01F;
  font-size: 0.7em;
  padding: 4px;
  text-decoration: underline dashed;
  text-decoration-color: transparent;
  text-underline-offset: 0.5em;
  transition: 0.3s;
}

.email:hover {
  cursor: pointer;
  text-decoration-color: #EAA01F;
}

/* copyright */
.copyright {
  color: #fafafa;
  font-size: 0.5em;
  text-transform: uppercase;
}


/* buttons */
.button {
  color: #EAA01F;
  font-size: 1em;
  font-weight: 500;
  min-width: 100px;
  background: none;
  border: 1px solid #EAA01F;
  padding: 0.4em 0.8em;
  margin: 0 20px 0;
  border-radius: 4px;
  transition: 0.3s;
}

.button:hover {
  cursor: pointer;
  background-color: #EAA01F;
  color: #212121;
}


/* ****************************************** */


/* making it responsive */
@media (max-width: 800px) {
  .nav {
    display: none;
  }

  h1 {
    font-size: 1.4em;
  }

  .bod {
    font-size: 1em;
    padding: 2em;
  }

  .contact--logo {
    height: 300px;
    margin: 50px;
  }
}